.card_body {
  width: 250px;
  height: 500px;
  background-color: white;
  margin-top: -160px;
  margin-left: 13%;
  box-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0);
  text-align: center;
  float: left;
}
.card_body h3 {
  margin-top: 10%;
}
.price {
  width: 200px;
  height: 100px;
  background-color: #7f53ac;
  background-image: linear-gradient(315deg, #7f53ac 0%, #647dee 74%);
  margin: auto;
  margin-top: 10%;
}
.price h2 {
  width: 99%;
  display: inline-block;
  margin-top: 7%;
  color: #eee;
}
.text {
  font-size: 0.7rem;
  width: 81%;
  margin: auto;
  margin-top: 10%;
}
hr {
  width: 81%;
  margin: auto;
  margin-top: 10%;
  height: 1px;
  background-color: #eee;
  border: none;
}
.features {
  font-size: 1.2rem;
  margin-top: 8%;
  color: gray;
  margin-left: 2rem;
  font-weight: 200;
  text-align: start;
}
.features span {
  color: blue;
}

.features h6:not(:last-child) {
  margin-bottom: 3%;
}
button {
  margin-top: 15%;
  padding: 0.5rem 0.8rem;
  border: none;
  background-image: linear-gradient(315deg, #7f53ac 0%, #647dee 74%);
  border-radius: 2px;
  box-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0);
  color: white;
  transition: all 0.3s;
}
button:hover {
  transform: translateY(-0.5rem);
}
button:active {
  transform: translateY(-0.2rem);
  box-shadow: 0 0.1rem 0.2rem rgba(0, 0, 0);
}
