* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.body {
  height: 50vh;
  background-color: #2a2a72;
  background-image: linear-gradient(
      315deg,
      rgba(42, 42, 114, 0.884) 0%,
      rgba(0, 160, 253, 0.856) 74%
    ),
    url(./images/img_1.jpg);
  text-align: center;
  color: #eee;
  font-size: 0.8rem;
}
.body h1 {
  margin-top: 1.5rem;
  display: inline-block;
  margin-bottom: 1.5rem;
}
.body p {
  width: 1000px;
  text-align: center;
  margin: auto;
}
